.blog__text {
  max-width: 475px;
  margin-left: 125px; }

@media only screen and (max-width: 768px) {
  .blog__text {
    margin-left: 95px; } }

.instagram__title {
  position: relative;
  text-align: center;
  margin: 0 auto 45px;
  font-size: 24px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 180px; }

.instagram__title::before {
  content: '';
  display: block;
  width: 30px;
  height: 30px;
  background-image: url(../images/icon.png);
  position: absolute;
  top: 6px;
  left: -40px; }

.instagram__wrapper {
  display: flex;
  margin-bottom: 10px;
  overflow-x: scroll; }

.instagram__picture {
  position: relative;
  display: flex;
  width: 384px;
  height: 384px; }

.instagram__picture::before {
  content: '';
  display: block;
  width: 30px;
  height: 30px;
  background-image: url(../images/icon-insta.png);
  background-repeat: no-repeat;
  position: absolute;
  bottom: 20px;
  right: 20px; }

.footer__top {
  background-color: #fead2d; }

.footer__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  padding: 80px 0 95px; }

.footer__logo {
  flex-basis: 28%; }

.footer__info {
  flex-basis: 18%; }

.footer__title {
  font-size: 26px;
  align-items: center;
  margin-bottom: 20px;
  justify-content: center; }

.footer__payment {
  flex-basis: 23%; }

.footer__box {
  flex-basis: 29%; }

.footer__newletter {
  max-width: 495px;
  width: 100%;
  height: 60px;
  text-indent: 15px;
  background-image: url(../images/convert.png);
  background-repeat: no-repeat;
  background-position: 20px;
  padding-left: 60px;
  font-size: 18px; }

.footer__submit {
  display: none; }

.footer__form {
  position: relative; }

.footer__btn {
  font-size: 18px;
  margin-right: 43px;
  text-transform: uppercase;
  color: #6d450c;
  position: relative; }

.footer__btn::after {
  content: '';
  background-image: url(../images/arrow-footer.png);
  background-repeat: no-repeat;
  position: absolute;
  width: 56px;
  height: 45px;
  top: 70px;
  left: 115px; }

/* 
.footer__link {
    color:#ffffff;
} */
.footer__inner {
  display: flex;
  justify-content: space-between;
  align-items: center; }

.footer__social-list {
  gap: 22px; }

.footer__bottom {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #6d301c;
  height: 80px; }

.footer__small {
  font-family: Muli;
  font-size: 16px;
  line-height: 35px;
  font-weight: 400;
  letter-spacing: 0.02em;
  color: #af8a82; }

.footer__link {
  width: 53px;
  height: 53px;
  position: fixed;
  bottom: 25px;
  right: 100px;
  background-color: #f2662f;
  border-radius: 5px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px; }

.footer__link::after {
  content: 'Go top';
  display: block;
  position: absolute;
  left: -62px;
  top: 34px;
  font-size: 16px;
  background-image: url(../images/icons-footer.png);
  background-repeat: no-repeat;
  line-height: 40px;
  font-weight: 700;
  letter-spacing: -0.02em;
  color: #ffffff; }

.footer__link::before {
  content: '';
  background: no-repeat center/cover url(../images/arrow-white.png);
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 13px;
  height: 25px;
  background-color: #ffffff; }
